body {
  width: 100%;
  height: 100vh;
  background-color: #0D0628;
}

#root {
  width: 100%;
  height: 100vh;
}

main {
  width: 100%;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: auto;
}

.container {
  width: 750px;
  height: 480px;
  background-color: #C4C1C1;
  margin: auto;
  border-radius: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.container-timer-control {
  width: 500px;
  height: 200px;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.timer-control-detail {
  width: 100%;
}

.timer-control-detail h2 {
  text-align: center;
}

.timer-control-info {
  text-align: center;
}

.session {
  height: 200px;
}

.session h2 {
  text-align: center;
}

.session-time-left {
  text-align: center;
  font-size: 2.5em;
}

.session-control-buttons {
  text-align: center;
}
