$mainBackgroundColor: #0D0628
$containerBackgroundColor: #C4C1C1

@mixin generalWidthHeight
    width: 100%
    height: 100vh

body
    @include generalWidthHeight
    background-color: $mainBackgroundColor

#root
    @include generalWidthHeight

main
    @include generalWidthHeight
    display: flex
    flex-direction: column
    justify-content: center
    margin: auto

.container
    width: 750px
    height: 480px
    background-color: $containerBackgroundColor
    margin: auto
    border-radius: 10px
    display: flex
    flex-direction: column
    justify-content: center

.container-timer-control
    width: 500px
    height: 200px
    margin: 0 auto
    display: flex
    flex-direction: row
    justify-content: center

.timer-control-detail
    width: 100%
    h2
        text-align: center

.timer-control-info
    text-align: center

.session
    height: 200px
    h2
        text-align: center

.session-time-left
    text-align: center
    font-size: 2.5em

.session-control-buttons
    text-align: center
